'use strict';

import React from 'react';
import { connect } from 'react-redux';
import * as actions from '../../actions/praiseSend';
import Breadcrumb from 'qnui/lib/breadcrumb';
import Form from 'qnui/lib/form';
import { Group as RadioGroup } from 'qnui/lib/radio';
import Input from 'qnui/lib/input';
import Button from 'qnui/lib/button';
import Pagination from 'qnui/lib/pagination';
import Select from 'qnui/lib/select';
import { RangePicker } from 'qnui/lib/date-picker';
import Field from 'qnui/lib/field';
import Icon from 'qnui/lib/icon';
import Tag from 'qnui/lib/tag';
import Table from 'qnui/lib/table';
import Tab from 'qnui/lib/tab';
import Dialog from 'qnui/lib/dialog';
import Search from 'qnui/lib/search';
import {Row, Col} from 'qnui/lib/grid';
import LinkTool from 'utils/linkTools';
import './praiseSend.scss';
const TabPane = Tab.TabPane;

const FormItem = Form.Item;
const paginationChange = function(value) {
    console.log(value);
};
const list = [
    {
        value: 'allPraise',
        label: ''
    },
    {
        value: 'wordPraise',
        label: ''
    }
];
const dataSource = [
    {label:'', value:'prize_cp'},
    {label:'', value:'prize_sjll'},
    {label:'', value:'prize_hf'}
];
const dataSource1 = [
    {label:'', value:'prize_ty'},
    {label:'', value:'prize_jt'}
];
const dataSource2 = [
    {label:'', value:'prize_yuan'},
    {label:'', value:'prize_jian'}
];


class PraiseSend extends React.Component {
    field = new Field(this);
    handleSubmit(e) {
        e.preventDefault();
        console.log('', this.field.getValues());
        this.field.validate();
    };

    constructor(props) {
        super(props);

        this.state = {
            radioValue: 'allPraise',
            selectValue: 'option1',
            searchValue: '',
            allItemVisible: true,
            someItemXuanzeCount: 0,
            someItemPaichuCount: 0,
            someItemDialogVisible: false,
            canjiaType:'primary',
            canjiaValue:'',
            paichuType:'primary',
            paichuValue:'',
            prize1Name:'',
            prize2Name:'',
            prize3Name:'',
            prize1Visible:false,
            prize2Visible:false,
            prize3Visible:false,
            prize1Type:1,
            prize2Type:1,
            prize3Type:1,
        };

        this.onRadioChange = this.onRadioChange;

        this.onSelectChange = this.onSelectChange;
    };
    onSearch(value) {
      this.setState({
        searchValue: value
      })
    }
    onRadioChange(value) {
        this.setState({
            radioValue: value
        });
        console.log(value);
    };
    onSelectChange(value) {
        this.setState({
            selectValue: value
        });
        console.log(value);
    };
    setLottery(){
      console.log('setLottery');
    };
    allItemClick=(value)=>{
      console.log('allClick');
      this.setState({
        allItemVisible:true
      });
    };
    someItemClick=(value)=>{
      console.log('someClick');
      this.setState({
        allItemVisible:false,
        someItemDialogVisible: true
      });
    };
    setPrize1=(value)=>{
      this.setState({
        prize1Visible:true
      });
      console.log(value);
    }
    prize1Change=(value,data)=>{
      let prizeName='';
      let prizeType=0;
      switch(value){
        case 'prize_cp':
        prizeName='';
        prizeType=1;
        break;
        case 'prize_sjll':
        prizeName='';
        prizeType=2;
        break;
        case 'prize_hf':
        prizeName='';
        prizeType=3;
        break;
        default:
        prizeType=0;
        break;
      }
      this.setState({
        prize1Name:prizeName,
        prizeType:prizeType
      })
      console.log('value:'+value+',data:'+data.value)
        console.log(this.state.prize1Name);
    }
    setPrize2=(value)=>{
      this.setState({
        prize2Visible:true
      });
      console.log(value);
    }
    prize2Change=(value,data)=>{
      let prizeName='';
      switch(value){
        case 'prize_cp':
        prizeName='';
        break;
        case 'prize_sjll':
        prizeName='';
        break;
        case 'prize_hf':
        prizeName=''
        break;
        default:
        break;

      }
      this.setState({
        prize2Name:prizeName
      })
    }
    setPrize3=(value)=>{
      this.setState({
        prize3Visible:true
      });
      console.log(value);
    }
    prize3Change=(value,data)=>{
      let prizeName='';
      switch(value){
        case 'prize_cp':
        prizeName='';
        break;
        case 'prize_sjll':
        prizeName='';
        break;
        case 'prize_hf':
        prizeName=''
        break;
        default:
        break;

      }
      this.setState({
        prize3Name:prizeName
      })
    }
    prize1DialogClose=()=>{
      this.setState({
        prize1Visible:false
      })
    }
    prize2DialogClose=()=>{
      this.setState({
        prize2Visible:false
      })

    }
    prize3DialogClose=()=>{
      this.setState({
        prize3Visible:false
      })

    }

    render() {
      const renderItemName = value => {
          if (value == '2016') {
              return '';
          }
          return value;
      }
      const renderItemPicture = value => {
          return value;
      }
      const renderItemPrice = value => {
          return value;
      }
      const renderItemOpration = value => {
          return value;
      }
      const someItemCallback=(key)=> {
          if('paichu'==key){
              console.log('');
          }else{
              console.log('');
          }
      };
      const someItemDialogClose = () => {
          this.setState({
              someItemDialogVisible: false
          });
          console.log('someItemDialogClose');
      };
        const init = this.field.init;

        const formItemLayout = {
            labelCol: {span: 6},
            wrapperCol: {span: 14},
        };

        const insetItemLayout = {
            labelCol: {span: 6},
            wrapperCol: {span: 18},
        };
        const someItemFooter = <Button type='normal' onClick={someItemDialogClose}></Button>;
        return (
            <div className='redux-demo-home'>

                <Breadcrumb style={{padding:20}}>
                    <Breadcrumb.Item link={LinkTool['index']}></Breadcrumb.Item>
                    <Breadcrumb.Item link={LinkTool['router-praiseSend']}></Breadcrumb.Item>
                </Breadcrumb>

                <Form >
                    <FormItem label='' labelCol={{ span: 6 }}>
                        <RangePicker/> <span style={{color:'orange'}}>  </span>
                    </FormItem>

                    <FormItem label='' labelCol={{ span: 6 }}>
                        <RadioGroup dataSource={list} value={this.state.radioValue} onChange={this.onRadioChange.bind(this)} />
                    </FormItem>

                    <FormItem label='' labelCol={{ span: 6 }}>
                        <Input style={{width:30}}></Input><span style={{color:'gray'}}> (0) </span>
                    </FormItem>

                    <FormItem label='' labelCol={{ span: 6 }}>
                      <div>
                        <Select style={{verticalAlign:'top'}} defaultValue='prize_jt' dataSource={dataSource1}></Select><span style={{verticalAlign:'middle'}}>&nbsp;&nbsp;</span>
                        <br></br>
                        <div style={{verticalAlign:'bottom', paddingTop:5}}>&nbsp;&nbsp;<Input style={{width:60, verticalAlign:'top'}}></Input>&nbsp;<Select dataSource={dataSource2} defaultValue='prize_jian'></Select>&nbsp;,&nbsp;&nbsp;<Select onChange={this.prize1Change} defaultValue='prize_cp' style={{verticalAlign:'top'}} dataSource={dataSource}></Select>&nbsp;&nbsp;<Button onClick={this.setPrize1} type='primary'>&nbsp;&nbsp;{this.state.prize1Name}&nbsp;&nbsp;</Button></div>
                        <Dialog style={{width:700,height:600}} visible = {this.state.prize1Visible}
                            onClose = {this.prize1DialogClose} onOk = {this.prize1DialogClose} onCancel = {this.prize1DialogClose} title = {'1'}>

                        </Dialog>
                        <br></br>
                        <div style={{paddingTop:5}}>&nbsp;&nbsp;<Input style={{width:60, verticalAlign:'top'}}></Input>&nbsp;&nbsp;,&nbsp;&nbsp;<Select onChange={this.prize2Change} defaultValue='prize_cp' style={{verticalAlign:'top'}} dataSource={dataSource}></Select>&nbsp;&nbsp;<Button onClick={this.setPrize2} type='primary'>&nbsp;&nbsp;{this.state.prize2Name}&nbsp;&nbsp;</Button></div>
                        <Dialog style={{width:700,height:600}} visible = {this.state.prize2Visible}
                            onClose = {this.prize2DialogClose} onOk = {this.prize2DialogClose} onCancel = {this.prize2DialogClose} title = '2'>

                        </Dialog>
                        <br></br>
                        <div style={{paddingTop:5}}>&nbsp;&nbsp;<Input style={{width:60, verticalAlign:'top'}}></Input>&nbsp;&nbsp;,&nbsp;&nbsp;<Select onChange={this.prize3Change} defaultValue='prize_cp' style={{verticalAlign:'top'}} dataSource={dataSource}></Select>&nbsp;&nbsp;<Button onClick={this.setPrize3} type='primary'>&nbsp;&nbsp;{this.state.prize3Name}&nbsp;&nbsp;</Button></div>
                        <Dialog style={{width:700,height:600}} visible = {this.state.prize3Visible}
                            onClose = {this.prize3DialogClose} onOk = {this.prize3DialogClose} onCancel = {this.prize3DialogClose} title = '3'>
                            {/*<div style={{display:(this.state.prize3Name='prize_sjll')?'block':'none'}}>
                              
                            </div>
                            <div style={{display:(this.state.prize3Name='prize_cp')?'block':'none'}}>
                              
                            </div>
                            <div style={{display:(this.state.prize3Name='prize_hf')?'block':'none'}}>
                              
                            </div>
                          */}
                        </Dialog>
                      </div>
                    </FormItem>

                    <FormItem label='' labelCol={{ span: 6 }}>
                      <Input style={{width:30}}></Input>&nbsp;&nbsp;(&nbsp;,,0)<Icon/>
                    </FormItem>

                    <FormItem label='' labelCol={{ span: 6 }}>
                      <div>
                        <Tag shape='selectable' selected={this.state.allItemVisible} onSelect={this.allItemClick}></Tag>
                        &nbsp;&nbsp;
                        <Tag shape='selectable' selected={!this.state.allItemVisible} onSelect={this.someItemClick}></Tag>
                        <div style={{display:this.state.allItemVisible?'none':'block'}}>
                        <Dialog style={{width:700,height:600}} visible = {this.state.someItemDialogVisible}
                            footer = {someItemFooter}
                            onClose = {someItemDialogClose} title = ''>
                            <Tab type='wrapped' onChange={someItemCallback}>
                               <TabPane tab='' key='paichu'>
                                    <span>{this.state.someItemPaichuCount}</span>
                                    <div style={{padding:5}}>
                                        <Search size={this.state.primarySize} inputWidth={80} defaultValue=''
                                        onSearch={this.onSearch} onChange={this.onChange}
                                        dataSource={this.state.dataSource} searchText='' />
                                    </div>

                                    <Table dataSource={dataSource2}>
                                        <Table.Column style={{width:60}} title='' dataIndex='itemPicture' cell={renderItemPicture}/>
                                        <Table.Column style={{width:120}} title='' dataIndex='itemName' cell={renderItemName}/>
                                        <Table.Column style={{width:60}} title='()' dataIndex='itemPrice' cell={renderItemPrice}/>
                                        <Table.Column style={{width:60}} title='' dataIndex='itemOpration' cell={renderItemOpration}/>
                                    </Table>
                               </TabPane>
                               <TabPane tab='' key='xuanze'>
                                    <span>{this.state.someItemXuanzeCount}</span>
                                    <div style={{padding:5}}>
                                        <Search style={{width:100,float:'left'}} size={this.state.primarySize} inputWidth={80} defaultValue=''
                                            onSearch={this.onSearch} onChange={this.onChange}
                                            dataSource={this.state.dataSource} searchText='' />
                                    </div>
                                    <Table dataSource={dataSource1}>
                                        <Table.Column style={{width:60}} title='' dataIndex='itemPicture' cell={renderItemPicture}/>
                                        <Table.Column style={{width:120}} title='' dataIndex='itemName' cell={renderItemName}/>
                                        <Table.Column style={{width:60}} title='()' dataIndex='itemPrice' cell={renderItemPrice}/>
                                        <Table.Column style={{width:60}} title='' dataIndex='itemOpration' cell={renderItemOpration}/>
                                    </Table>
                               </TabPane>
                            </Tab>
                            <Pagination style={{paddingTop:20, textAlign:'center'}} defaultCurrent={1} onChange={paginationChange} />
                        </Dialog>
                        </div>
                      </div>
                    </FormItem>

                    <FormItem label='' labelCol={{ span: 6 }}>

                    </FormItem>

                    <Row style={{ marginTop: 24 }}>
                        <Col offset='6'>
                            <Button type='primary' onClick={this.handleSubmit.bind(this)}></Button>
                        </Col>
                    </Row>
                </Form>

            </div>
        );
    }
}

export default connect((state)=> {
  return {
    praiseSend: state.praiseSend
  };
})(PraiseSend);



